import * as React from 'react';
import { useLocriaStore } from './LocriaStore';

export const LocriaOSMD = () => {
  const { osmdConnect } = useLocriaStore(s => s.actions);
  const div = React.useRef<HTMLDivElement>(null!);
  console.log('[LocriaOSMD] render');

  React.useEffect(() => {
    osmdConnect(div.current);
  }, [div, osmdConnect]);

  return (
    <div ref={div}></div>
  );
};