import * as React from 'react';
import { useLocriaStore } from './LocriaStore';

export const LocriaPlayback = () => {
  const { loaded: osmdLoaded } = useLocriaStore(s => s.osmd);
  const { loaded: midiLoaded } = useLocriaStore(s => s.midi);
  const { osmdCursorNext, osmdCursorPrevious, osmdCursorReset, locriaPlay, locriaStop } = useLocriaStore(s => s.actions);
  console.log('[LocriaPlayback] render');

  return (
    <div className="d-flex justify-content-center">
      {!(osmdLoaded && midiLoaded)
        ? (
          <div className="m-1">Loading...</div>
        ) : (
          <div className="btn-toolbar m-1" role="toolbar">
            <div className="btn-group btn-group-sm mr-2">
              <button type="button" className="btn btn-secondary" onClick={locriaPlay}>
                <i className="fas fa-play fa-fw"></i>
              </button>
              <button type="button" className="btn btn-secondary" onClick={locriaStop}>
                <i className="fas fa-stop fa-fw"></i>
              </button>
            </div>
            <div className="btn-group btn-group-sm">
              <button type="button" className="btn btn-secondary" onClick={osmdCursorReset}>
                <i className="fas fa-fast-backward fa-fw"></i>
              </button>
              <button type="button" className="btn btn-secondary" onClick={osmdCursorPrevious}>
                <i className="fas fa-step-backward fa-fw"></i>
              </button>
              <button type="button" className="btn btn-secondary" onClick={osmdCursorNext}>
                <i className="fas fa-step-forward fa-fw"></i>
              </button>
            </div>
          </div>
        )}
    </div>
  );
};