import { OpenSheetMusicDisplay } from 'opensheetmusicdisplay';
import create from 'zustand'
import { player as WebAudioFontPlayer, Envelope } from '../@types/webaudiofont/player';
import { groupBy } from './Utils';

export type IMIDISounds = {
  audioContext: AudioContext;
  player: WebAudioFontPlayer;
};

export type LocriaStoreOSMD = {
  instance: OpenSheetMusicDisplay;
  connected: boolean;
  loaded: boolean;
};

export type LocriaStoreMIDI = {
  instance: IMIDISounds;
  connected: boolean;
  loaded: boolean;
  envelopes: Envelope[];
  playing: boolean;
};

export type LocriaStoreActions = {
  osmdUpdate(state: Partial<LocriaStoreOSMD>): void;
  osmdConnect(div: HTMLDivElement): void;
  osmdLoad(file: string): void;
  osmdCursorNext(): void;
  osmdCursorPrevious(): void;
  osmdCursorReset(): void;
  midiUpdate(state: Partial<LocriaStoreMIDI>): void;
  midiConnect(midi: IMIDISounds): void;
  locriaPlay(): void;
  locriaStop(): void;
}

export type LocriaNote = {
  instrument: number;
  time: number;
  note: number;
  duration: number;
}

export type LocriaStoreUtils = {
  osmdGetNotes(): [LocriaNote[], number[]];
  midiInstrumentPreset(index: number): any;
};

export type LocriaStore = {
  osmd: LocriaStoreOSMD;
  midi: LocriaStoreMIDI;
  actions: LocriaStoreActions;
  utils: LocriaStoreUtils;
};

const samplePath = 'assets/Angel_Beats_-_My_Soul_Your_Beats.xml';

export const useLocriaStore = create<LocriaStore>((set, get) => ({
  osmd: {
    instance: null!,
    connected: false,
    loaded: false
  },
  midi: {
    instance: null!,
    connected: false,
    loaded: false,
    envelopes: [],
    playing: false
  },
  actions: {
    osmdUpdate(state: Partial<LocriaStoreOSMD>) { set({ osmd: { ...get().osmd, ...state } }); },
    osmdConnect(div: HTMLDivElement) {
      console.log('[OSMD] attaching osmd to div', div);
      get().actions.osmdUpdate({ instance: new OpenSheetMusicDisplay(div, {}), connected: true });
      get().actions.osmdLoad(samplePath);
    },
    osmdLoad(file: string) {
      get().osmd.instance.load(file).then(() => {
        get().osmd.instance.render();
        get().osmd.instance.cursor.show();
        get().actions.osmdUpdate({ loaded: true });
      });
    },
    osmdCursorNext() { get().osmd.instance.cursor.next(); },
    osmdCursorPrevious() {
      const { osmd: { instance: { cursor } } } = get();
      const { iterator: { currentTimeStamp: { RealValue: currentTimestamp } } } = cursor;

      if (currentTimestamp > 0) {
        let timestampIndex = 0;

        cursor.reset();
        while (cursor.iterator.currentTimeStamp.RealValue !== currentTimestamp) {
          timestampIndex++;
          cursor.next();
        }
        console.log('timestampIndex', timestampIndex);
        timestampIndex--;
        cursor.reset();
        while (timestampIndex > 0) {
          timestampIndex--;
          cursor.next();
        }
      }
    },
    osmdCursorReset() { get().osmd.instance.cursor.reset(); },
    midiUpdate(state: Partial<LocriaStoreMIDI>) { set({ midi: { ...get().midi, ...state } }) },
    midiConnect(midi: IMIDISounds) {
      if (midi.audioContext.state === 'running') {
        midi.audioContext.suspend();
      }
      get().actions.midiUpdate({ instance: midi, connected: true });
      midi.player.loader.waitLoad(() => get().actions.midiUpdate({ loaded: true }));
    },
    locriaPlay() {
      const { midi: { instance: midi } } = get();
      const [allNotes, allTimestamps] = get().utils.osmdGetNotes();

      get().actions.locriaStop();

      if (allNotes.length > 0) {
        const envelopes: Envelope[] = [];
        const endingNote = allNotes[allNotes.length - 1];
        const handleCursor = () => {
          if (get().midi.playing) {
            if (allTimestamps.length > 0) {
              if (midi.audioContext.currentTime >= allTimestamps[0]) {
                allTimestamps.shift();
                get().osmd.instance.cursor.next();
              }
              setTimeout(handleCursor, 0);
            } else {
              get().actions.locriaStop();
            }
          }
        };

        allTimestamps.shift();
        allTimestamps.forEach((timestamp, index) => allTimestamps[index] = timestamp + midi.audioContext.currentTime);
        allTimestamps.push(endingNote.time + endingNote.duration + midi.audioContext.currentTime);
        midi.audioContext.resume();

        for (const [, instrumentNotes] of Object.entries(groupBy(allNotes, x => x.instrument))) {
          const preset = get().utils.midiInstrumentPreset(instrumentNotes[0].instrument);
          instrumentNotes.forEach(note => {
            envelopes.push(midi.player.queueWaveTable(
              midi.audioContext,
              midi.audioContext.destination,
              preset,
              midi.audioContext.currentTime + note.time,
              note.note,
              note.duration,
              0.1
            )!);
          });
        }

        get().actions.midiUpdate({ envelopes: envelopes, playing: true });
        handleCursor();
      }
    },
    locriaStop() {
      get().midi.envelopes.forEach(envelope => envelope.cancel!());
      get().actions.midiUpdate({ envelopes: [], playing: false });
      get().midi.instance.audioContext.suspend();
    }
  },
  utils: {
    osmdGetNotes() {
      const { osmd: { instance: { cursor } } } = get();
      const start = cursor.iterator.currentTimeStamp.RealValue;
      const timestamps: number[] = [];
      const notes: LocriaNote[] = [];

      console.log('gathering notes starting at', start);

      // start playing from cursor's current position
      for (const iterator = cursor.iterator.clone(); !iterator.EndReached; iterator.moveToNext()) {
        // skip notes up to the cursor (apparently, clone does NOT put iterator at same position)
        if (iterator.currentTimeStamp.RealValue < start) {
          continue;
        }
        //TODO: read bpm from sheet; figure out how to add to music xml file
        const bpm = 145;
        // add a timestamp for the current cursor position, to be played back with the audio
        timestamps.push((iterator.currentTimeStamp.RealValue - start) * 4 * 60 / bpm);
        // process all voice entries at the iterator's position
        for (const voice of iterator.CurrentVoiceEntries) {
          // filter out empty notes and notes which produce no sounds
          for (const note of voice.Notes.filter(note => note !== null && note.halfTone !== 0)) {
            // if the note is involved in a tie, we only want the first note
            if (!note.NoteTie || note.NoteTie.Notes[0] === note) {
              // if note is involved in a tie, use the tie's duration; otherwise, use note's duration
              const duration = (note.NoteTie ? note.NoteTie.Duration.RealValue : note.Length.RealValue) * 4 * 60 / bpm;
              notes.push({
                instrument: note.ParentStaff.ParentInstrument.MidiInstrumentId,
                time: (iterator.currentTimeStamp.RealValue - start) * 4 * 60 / bpm,
                note: note.halfTone + 12,
                duration: duration
              });
            }
          }
        }
      }
      return [notes, timestamps];
    },
    midiInstrumentPreset(index: number) { return (window as any)[get().midi.instance.player.loader.instrumentInfo(index).variable]; }
  }
}));