import * as React from 'react';
import MIDISounds from 'midi-sounds-react';
import { useLocriaStore } from './LocriaStore';

export const LocriaMIDI = () => {
  const { midiConnect } = useLocriaStore(s => s.actions);
  console.log('[LocriaMIDI] render');

  return (
    <div className="d-none">
      <MIDISounds
        ref={(ref: any) => midiConnect(ref)}
        instruments={[0]}
      />
    </div>
  );
};