import * as React from 'react';

export type SetStateType<T> = (state: React.SetStateAction<T>, callback?: (state: T) => void) => void;
export function useStateWithSetStateCallback<T>(initialState: T = null!): [T, SetStateType<T>] {
  const [state, setState] = React.useState(initialState);
  const callbackRef = React.useRef<(state: T) => void>();

  const setStateWithCallback: SetStateType<T> = React.useCallback((state, callback) => {
    callbackRef.current = callback;
    setState(state);
  }, []);

  React.useEffect(() => {
    if (callbackRef.current) {
      callbackRef.current(state);
      callbackRef.current = null!;
    }
  }, [state]);

  return [state, setStateWithCallback];
}

export const groupBy = <T, K extends keyof any>(list: T[], getKey: (item: T) => K) =>
  list.reduce((previous, currentItem) => {
    const group = getKey(currentItem);
    if (!previous[group]) {
      previous[group] = [];
    }
    previous[group].push(currentItem);
    return previous;
  }, {} as Record<K, T[]>);